<template>
	<div class="fixWheelContainer">
		<div class="shadowRight"></div>
		<div class="shadowTop"></div>
		<div class="spinnerOut" :style="spinnerOut">
			<img src="@/assets/image/main/fixWheel/spinnerOut.webp" alt="" />
		</div>
		<div class="spinnerInner" :style="spinnerInner">
			<img src="@/assets/image/main/fixWheel/spinnerInner.webp" alt="" />
		</div>
		<div class="spinnerLogo">
			<img src="@/assets/image/main/fixWheel/logoEcosystem.svg" alt="" />
		</div>
	</div>
</template>
<style scoped>
	.shadowRight,
	.shadowTop {
		display: flex;
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: 8;
	}
	.shadowRight {
		background: linear-gradient(
			90deg,
			rgba(18, 18, 18, 1) 7%,
			rgba(18, 18, 18, 0) 50%,
			rgba(18, 18, 18, 1) 93%
		);
	}
	.shadowTop {
		background: linear-gradient(
			0deg,
			rgba(18, 18, 18, 1) 7%,
			rgba(18, 18, 18, 0) 50%,
			rgba(18, 18, 18, 1) 93%
		);
	}
	.spinnerLogo {
		display: flex;
		position: absolute;
		z-index: 9;
	}
	.fixWheelContainer {
		display: flex;
		width: 100%;
		height: 1000px;
		position: relative;
		overflow: hidden;
		align-items: center;
		justify-content: center;
	}
	.spinnerOut,
	.spinnerInner {
		display: flex;
		position: absolute;
	}
</style>
<script scoped>
	export default {
		data() {
			return {
				spinnerOut: {
					transform: "rotate(0deg)", // Başlangıçta dönüş açısı
					transformOrigin: "center", // Dönüş merkezi
				},
				spinnerInner: {
					transform: "rotate(0deg)", // Başlangıçta dönüş açısı
					transformOrigin: "center", // Dönüş merkezi
				},
				scrollListener: null,
			};
		},
		mounted() {
			this.scrollListener = () => {
				const scrollY = window.scrollY;
				const rotationAngle = scrollY / 12;
				const rotationNegativeAngle = -scrollY / 12;
				this.spinnerOut.transform = `rotate(${rotationAngle}deg)`;
				this.spinnerInner.transform = `rotate(${rotationNegativeAngle}deg)`;
			};

			window.addEventListener("scroll", this.scrollListener);
		},
	};
</script>
