<template>
	<div class="contactContainer">
		<div class="title">Bir Telefon Kadar Yakınız</div>
		<div class="contactContainerInner">
			<div
				v-for="icon in icons"
				:key="icon.id"
				class="contactCard"
				@mouseenter="setOpacity(icon.id)"
				@mouseleave="fullOpacity()"
				:style="{ opacity: icon.oppacity }">
				<div><img :src="getIconPath(icon.iconName)" alt="" /></div>
				<div class="innerTitle">{{ icon.title }}</div>
				<div class="innerText">{{ icon.text }}</div>
			</div>
		</div>
	</div>
</template>
<style scoped>
	.contactContainerInner {
		display: flex;
		perspective: 1000px;
		width: 100%;
		height: 100%;
	}
	.contactCard {
		position: absolute;
		display: flex;
		flex-direction: column;
		margin: 0px -40px;
		transform: rotateY(45deg);
		transform-origin: center center;
		transition: all 0.2s ease-in-out;
		background: linear-gradient(
			297deg,
			rgba(35, 35, 35, 1) 0%,
			rgba(78, 78, 78, 1) 100%
		);
		border-radius: 12px;
		width: 223px;
		height: 160px;
		padding: 10px;
		box-sizing: border-box;
		cursor: pointer;
	}
	.contactCard:first-child {
	}
	.contactCard:nth-child(2) {
		left: 110px;
	}
	.contactCard:last-child {
		left: 210px;
	}
	.contactCard:hover {
		width: 100%;
		height: 100%;
		transform: rotateY(0deg);
		margin: 0px;
		left: 0;
	}

	.hovered {
		display: none !important;
	}

	.innerText {
		font-size: 20px;
		color: #d1d1d1;
		z-index: 3;
	}
	.innerTitle {
		color: white;
		font-size: 17px;
		font-weight: bold;
	}
	.contactContainer {
		display: flex;
		width: 492px;
		height: 354px;
		background-color: #232323;
		border-radius: 12px;
		font-size: 40px;
		color: white;
		font-weight: light;
		align-content: center;
		justify-content: flex-start;
		align-items: center;
		flex-direction: column;
		padding: 50px 63px;
		box-sizing: border-box;
		text-align: center;
	}
	.title {
		margin-bottom: 42px;
		font-size: 24px;
		color: #8b8b8b;
	}
</style>
<script scoped>
	export default {
		data() {
			return {
				icons: [
					{
						id: 1,
						iconName: "call",
						title: "Çağrı Merkezimiz",
						text: "0 (850) 532 05 00",
						oppacity: 1,
					},
					{
						id: 2,
						iconName: "mail",
						title: "E-Posta Adresimiz",
						text: "info@deltaservis.com.tr",
						oppacity: 1,
					},
					{
						id: 3,
						iconName: "wp",
						title: "WhatsApp Destek Hattımız",
						text: "0 (850) 532 05 00",
						oppacity: 1,
					},
				],
			};
		},
		methods: {
			fullOpacity() {
				this.icons.forEach((icon) => {
					icon.oppacity = 1;
				});
			},
			setOpacity(id) {
				this.icons.forEach((icon) => {
					if (icon.id !== id) {
						icon.oppacity = 0;
					}
				});
			},
			getIconPath(iconName) {
				return require(`@/assets/image/main/whereAreWe/${iconName}.svg`);
			},
		},
	};
</script>
