<template>
	<div class="contain">
		<div class="animationContainer">
			<img :src="currentIcon" alt="" />
		</div>
		<div class="buttonContainer">
			<div
				v-for="(button, index) in buttons"
				:class="{ buttonItem: true, selected: selectedIndex === index }"
				@click="toggleCss(button.id, index)"
				:key="button.id">
				{{ button.name }}
			</div>
		</div>
	</div>
</template>
<style scoped>
	.animationContainer img {
		-o-object-fit: contain;
		object-fit: contain;
		width: 456px;
		max-height: 456px;
	}
	.contain {
		display: flex;
		justify-content: space-evenly;
	}
	.animationContainer {
		display: flex;
		width: 50%;
		height: 456px;
		justify-content: center;
		align-items: flex-start;
	}
	.buttonContainer {
		display: flex;
		flex-direction: column;
		width: 50%;
	}
	.buttonItem {
		font-size: 55px;
		color: #7b7b7b;
		font-weight: 700;
		cursor: pointer;
		margin-bottom: 20px;
	}
	.selected {
		color: white;
	}
</style>
<script scoped>
	export default {
		data() {
			return {
				iconName: 1,
				currentFrame: 1,
				buttons: [
					{ name: "Ekran Parlatma", id: "1" },
					{ name: "Batarya Değişimi", id: "2" },
					{ name: "Ekran Değişimi", id: "3" },
					{ name: "Anakart Onarımı", id: "4" },
					{ name: "Robot Süpürge Onarımı", id: "5" },
				],
				selectedIndex: 0,
			};
		},
		computed: {
			currentIcon() {
				return require(`@/assets/image/main/fourthBlog/icon${this.iconName}_${this.currentFrame}.svg`);
			},
		},
		methods: {
			toggleCss(iconName, index) {
				this.iconName = iconName;
				this.selectedIndex = this.selectedIndex === index ? null : index;
			},
			async updateIcon() {
				for (let i = 1; i <= 6; i++) {
					await new Promise((resolve) => setTimeout(resolve, 300));
					this.currentFrame = i;
					if (
						i == 6 ||
						(this.iconName == 3 && i == 5) ||
						(this.iconName == 4 && i == 5)
					) {
						i = 0;
					}
				}
			},
		},
		created() {
			this.updateIcon();
		},
	};
</script>
