<template>
	<div>
		<div class="containerText">
			<div>Hizmet verdiğimiz</div>
			<div>markalar ve modeller</div>
		</div>
		<div class="refBrandsContainer">
			<div
				v-for="(group, index) in groupedItems"
				:key="index"
				class="refBrandInnerContainer">
				<div v-for="item in group" :key="item">
					<div class="refBrandsInner">
						<img
							:src="getIconPath(item.icon)"
							alt=""
							:style="item.width ? 'width: ' + item.width : ''" />
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<style scoped>
	.refBrandInnerContainer {
		display: flex;
		row-gap: 10px;
		flex-direction: column;
	}

	.refBrandInnerContainer:nth-child(odd) {
		padding-top: 80px;
	}
	.refBrandsInner {
		display: flex;
		width: 231px;
		height: 160px;
		background-color: #212121;
		border-radius: 39px;
		justify-content: center;
		align-items: center;
		column-gap: 10px;
	}
	.refBrandsInner img {
		object-fit: contain;
		width: 65%;
	}
	.refBrandsContainer {
		column-gap: 10px;
		display: flex;
		width: 100%;
		justify-content: center;
		padding-bottom: 210px;
	}
	.containerText {
		display: flex;
		flex-direction: column;
		width: 100%;
		font-size: 70px;
		font-weight: bold;
		padding: 120px 0px;
	}
	.containerText div {
		text-align: center;
	}
	.containerText div:first-child {
		color: #e51a4b;
	}
	.containerText div:last-child {
		color: white;
	}
</style>
<script scoped>
	import { mapGetters } from "vuex";
	export default {
		methods: {
			getIconPath(iconFileName) {
				return require(`@/assets/image/main/refBrands/${iconFileName}`);
			},
		},
		created() {
			this.$store.dispatch("refBrands/getRefBrandItems");
		},
		computed: {
			...mapGetters({
				brandItem: "refBrands/_brandItems",
			}),
			groupedItems() {
				const result = [];

				for (let i = 0; i < this.brandItem.length; i += 2) {
					result.push([this.brandItem[i], this.brandItem[i + 1]]);
				}
				return result;
			},
		},
	};
</script>
