<template>
	<div class="bannerContainer">
		<div class="bannerText">
			<div>GARANTİLİ GÜVENİLİR,</div>
			<div>PROFESYONEL<br />ÇÖZÜM</div>
		</div>
		<div class="videoContainer">
			<div class="video" :style="getWidth">
				<video
					id="fullscreen-video"
					autoplay
					loop
					muted
					playsinline
					:style="getTransform">
					<source src="@/assets/videos/banner/banner.mp4" type="video/mp4" />
					Your browser does not support the video tag.
				</video>
			</div>
		</div>

		<div class="bannerShadow"></div>
	</div>
</template>
<style scoped>
	video {
		object-fit: contain;
		height: 130%;
		position: absolute;
		transition: all 1s ease-out;
	}
	.video {
		overflow: hidden;
		height: 100%;
		right: 0px;
		display: flex;
		position: absolute;
		align-content: center;
		align-items: center;
		transition: all 1s ease-out;
		border-radius: 30px;
	}
	.videoContainer {
		top: 85px;
		position: absolute;
		display: flex;
		width: 100%;
		height: 100%;
	}
	.bannerShadow {
		display: flex;
		width: 100%;
		height: 102px;
		position: absolute;
		z-index: 3;
		/* background-color: white; */
		bottom: 0px;
		background: linear-gradient(
			180deg,
			rgba(18, 18, 18, 0) 0%,
			rgba(18, 18, 18, 1) 100%
		);
	}
	.bannerContainer {
		padding-top: 85px;
		position: relative;
		overflow: hidden;
		display: flex;
		width: 100%;
		height: 90vh;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
	}
	.bannerContainer img {
		position: absolute;
		-o-object-fit: contain;
		object-fit: contain;
		width: 100%;
	}
	.bannerText {
		left: 10%;
		display: flex;
		position: absolute;
		z-index: 2;
		flex-direction: column;
		width: 80%;
		line-height: 1;
		animation: fade 4.5s linear;
	}
	@keyframes fade {
		0% {
			opacity: 0;
		}
		70% {
			opacity: 0;
		}
		100% {
			opacity: 1;
		}
	}
	.bannerText div:first-child {
		font-size: 42px;
		color: #e51a4b;
		font-weight: 500;
	}
	.bannerText div:last-child {
		font-size: 80px;
		color: white;
		font-weight: bold;
	}
</style>
<script>
	import { mapGetters } from "vuex";
	export default {
		data() {
			return {
				transform: 0,
				width: 100,
			};
		},
		computed: {
			getTransform() {
				return `transform: translate(${this.transform}%,0%);`;
			},
			getWidth() {
				return `width:${this.width}%`;
			},
			...mapGetters({
				loading: "loading/_loading",
			}),
		},
		watch: {
			loading() {
				if (this.loading == false) {
					setTimeout(() => {
						this.changeBanner();
					}, 3000);
				}
			},
		},
		methods: {
			changeBanner() {
				this.transform = -20;
				this.width = 50;
			},
		},
	};
</script>
