<template>
	<div>
		<div v-for="item in sliderItem" :key="item.id" class="sliderContainer">
			<div class="sliderInfo">
				<div class="sliderInfoTitle">{{ item.title }}</div>
				<div class="sliderInfoText">{{ item.text }}</div>
				<div class="sliderInfoLink">
					<a :href="item.button_link">{{ item.button_text }} </a>
				</div>
			</div>
			<div>
				<img :src="getImagePath(item.image)" alt="" />
			</div>
		</div>
	</div>
</template>
<script scoped>
	import { mapGetters } from "vuex";
	export default {
		methods: {
			getImagePath(imageFileName) {
				return require(`@/assets/image/main/innerSlider/${imageFileName}`);
			},
		},
		created() {
			this.$store.dispatch("innerSlider/getSliderItems");
		},
		computed: {
			...mapGetters({
				sliderItem: "innerSlider/_sliderItems",
			}),
		},
	};
</script>
<style scoped>
	.sliderContainer {
		position: relative;
		display: flex;
		align-items: center;
		width: 100%;
		overflow: hidden;
	}
	.sliderInfo {
		left: 20%;
		display: flex;
		flex-direction: column;
		position: absolute;
		z-index: 2;
		width: 30%;
		height: 100%;
		justify-content: center;
		row-gap: 15px;
	}
	.sliderInfoTitle {
		color: white;
		font-size: 70px;
		font-weight: bold;
	}
	.sliderInfoText {
		line-height: 1.5;
		color: #d1d1d1;
		font-size: 18px;
	}
	.sliderInfoLink {
		background: linear-gradient(
			180deg,
			rgba(229, 26, 75, 1) 0%,
			rgba(122, 8, 74, 1) 100%
		);
		display: flex;
		width: 427px;
		padding: 18px;
		justify-content: center;
		border-radius: 19px;
	}
	.sliderInfoLink a {
		color: white;
		text-decoration: none;
	}
</style>
